.App {
  text-align: center;
  font-family: 'Raleway', sans-serif;
}



.App-header {
  background: #34679b;
  height: 60px;
  padding:0 20px;
  color: white;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-align: center;
      align-items: center;
}

.App-main {
  color:white;
  padding: 30px
}

.App-title {
  font-size: 1.2em;
}

.App-intro {
  font-size: large;
}

.App-search{
  margin-top: 5px;
  background: #3e6fa0;
  border:none;
  height: 50px;
  padding: 0 20px;
}

.App-search-name{
  position: absolute;
  top:0;
  right: 250px;
  width: 180px;
}

.App-search-country{
  position: absolute;
  top:0;
  right: 180px;
  width: 20px;
}

.App-search-btn{
  position: absolute;
  top:0;
  right:80px;
}

.App-city{
  font-weight: 600;
  font-size: 2rem;
  margin:0;
}

.App-div-pic-temp{
 display: -ms-flexbox;
 display: flex;
 -ms-flex-pack: center;
     justify-content: center;
 -ms-flex-align: center;
     align-items: center;
}

.App-temperature{
  margin:0;
  font-weight: 500;
  font-size: 6rem;
  line-height: 4rem;
}

.App-temperature span{
  font-size:2rem;
}

.App-icon{
  margin-top: 20px;
  font-size:6rem!important;
}

.App-weather{
  font-size:2rem;
  margin:0;
}

.App-week{
  margin-top: 5px;
 display: -ms-flexbox;
 display: flex;
 -ms-flex-direction: row;
     flex-direction: row;
 -ms-flex-pack: center;
     justify-content: center;
 -ms-flex-wrap:wrap;
     flex-wrap:wrap;
}

.App-days{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  width: 200px;
  height: 200px;
  background-color:rgba(0, 0, 0, 0.1);
  margin:20px;
  border-radius: 10px;
}

.App-J{
  margin:0;
  padding: 0;
  font-size: 1.3rem;
}

.App-T-days{
  font-size: 2rem;
}

.App-W-days{
  font-size: 1rem;
}

.App-I-days{
  padding:0;
  margin-top: 10px;
  font-size: 3rem!important;
}

.App-bg{
  background: -webkit-gradient(linear, left top, right top, from(#444) , to(#aaa));
  background: -webkit-linear-gradient(left, #444 , #aaa);
  background: -o-linear-gradient(left, #444 , #aaa);
  background: linear-gradient(to right, #444 , #aaa);
}
@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

